/* ________________________________________________________ */

.clear {
  clear: both;
}

#main {
  background: #fff;
  width: 900px;
  padding: 35px;
  margin: 0 auto 15px auto;
  overflow: hidden;
  z-index: 1;
  box-shadow: 0 0 6px #ccc;
}

#title {
  line-height: 42px;
  border-bottom: 3px solid #000;
  padding-bottom: 30px;
  margin: 0px 0 30px 0;
  font-weight: bold;
}

#credit {
  margin-bottom: 20px;
  font-size: 11px;
  color: #999;
  text-align: center;
}
#credit a {
  color: #999;
}

p label {
  font-weight: bold;

  font-size: 110%;
  margin-right: 10px;
}
p label span.colon {
  display: inline;
}

.section {
  margin-bottom: 20px;
}

.item {
  margin: 0 0 20px 0;
}

/*  _____________________________ */
.rtl {
  direction: rtl;
}

.photo .basic-info .set {
  float: none !important;
  width: auto !important;
}
.basic-info .address {
  width: 40%;
  float: left;
}
.basic-info .r {
  float: right;
  width: 58%;
  text-align: right;
}
.basic-info label {
  font-weight: bold;
}
.basic-info label,
.basic-info span {
  display: inline !important;
}
.basic-info p {
  margin: 0;
}

.section .title {
  background: #000;
  float: left;
  text-transform: uppercase;
  font-size: 105%;
  font-weight: bold;
  word-wrap: break-word;
  color: #fff;
  padding: 4px 6px;
  width: 16%;
  margin-right: 30px;
}
.section .set {
  float: left;
  width: 78%;
}
.section .set h3 {
  margin: 0;
}
.section span.value {
  display: block;
}
.section h3 span.l,
.section span.r {
  width: 49%;
  float: left;
}
.section span.r {
  float: right;
  text-align: right;
}
.section-education .school,
.section-work .job_title {
  font-size: 100%;
  display: block;
  margin: 5px 0 5px 0;
}

/* ___ photo ___ */
#photo {
  width: 160px;
  height: 200px;
  overflow: hidden;
  float: left;
  margin-top: 8px;
  border-radius: 4px;
}
#photo img {
  width: 100%;
  height: auto;
}
.header.photo {
  margin-left: 190px;
}
.header.photo #title {
  text-align: left;
}
.header.photo .basic-info {
  float: none;
  padding: 0;
}

@media screen and (max-width: 950px) {
  body {
    background: #fff;
  }
  #main {
    box-shadow: none;
    padding: 0;
    padding: 5px;
  }
}

/* Outer wrapper for data and toggle */
#outerWrapper {
  display: flex;
  flex-direction: column;
  /* Text align works on any element type */
  text-align: center;
  width: 15.5vw;
  height: 32vh;
  position: fixed;
  top: 18vh;
  left: -5vw;
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(0, 0.52, 0, 1);
  transition: -webkit-transform 0.5s cubic-bezier(0, 0.52, 0, 1);
  transition: transform 0.5s cubic-bezier(0, 0.52, 0, 1);
  transition: transform 0.5s cubic-bezier(0, 0.52, 0, 1), -webkit-transform 0.5s cubic-bezier(0, 0.52, 0, 1);
  overflow: hidden;
  z-index: 5;
  /* debugging color frame */
  /* border: 3px solid red; */
}

#outerWrapper #toggle {
  position: absolute;
  top: 0vh;
}

/* Inner wrapper div */
#wordData {
  -webkit-transition: -webkit-transform 1s cubic-bezier(0, 0.52, 0, 1);
  transition: -webkit-transform 1s cubic-bezier(0, 0.52, 0, 1);
  transition: transform 1s cubic-bezier(0, 0.52, 0, 1);
  transition: transform 1s cubic-bezier(0, 0.52, 0, 1), -webkit-transform 1s cubic-bezier(0, 0.52, 0, 1);
  z-index: 5;
  width: 9vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  vertical-align: center;
  overflow: wrap;
  word-wrap: normal;
  border-radius: 3px;
  position: absolute;
  left: 0vw;
  top: 9vh;
  border: 4px solid black;
  box-shadow: 0 4px 4px rgb(0, 0, 0);
}

#wordData p {
  /* line-height: 1.5vh; */
  padding-top: 0.75vh;
  padding-left: 0.5vh;
  padding-right: 0.5vh;
  z-index: 5;
  text-align: center;
  justify-content: center;
  font-family: "Exo", "Ubuntu", "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
  -webkit-transition: -webkit-transform 0.05s;
  transition: -webkit-transform 0.05s;
  transition: transform 0.05s;
  transition: transform 0.05s, -webkit-transform 0.05s;
}

#wordData h5 {
  font-weight: bold;
}

#wordData p:hover,
#wordData h5:hover,
#wordData h6:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

#wordData.hide {
  -webkit-transform: translate3d(-10vw, 0, 0);
          transform: translate3d(-10vw, 0, 0);
}

#wordData.show {
  -webkit-transform: translate3d(5.2vw, 0, 0);
          transform: translate3d(5.2vw, 0, 0);
  /* Hide extra stuff if sticking out of container */
  overflow: hidden;
}

#toggle{
  position: absolute;
  z-index: 5;
  margin: 10px;
  border: 4px solid black !important;
  border-radius: 50px !important;
  -webkit-border-radius: 50px !important;
  -moz-border-radius: 50px !important;
  width: 47px;
  height: 47px;
  /* left: 3vw; */
  top: 5vh;
  left: 4.8vw;
  -webkit-transition: -webkit-transform 0.15s;
  transition: -webkit-transform 0.15s;
  transition: transform 0.15s;
  transition: transform 0.15s, -webkit-transform 0.15s;
  cursor: pointer;
  outline: none;
  box-shadow: 0 5px rgb(0, 0, 0);
}
#toggle:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #ffbf00;
  box-shadow: 0 3.7px 4px rgb(0, 0, 0);
}

#toggle:active {
  background-color: rgb(230, 176, 0);
  box-shadow: 0 5px 2px rgb(0, 0, 0);
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

