body {
  align-items: center;
  background: mintcream;
  margin: 0;
  font-family: "Exo", "Ubuntu", "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* body > #root > div {
