code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Custom image settings */
#mainLogo,
#bioImage {
  /* puts item on top of css backgrounds */
  z-index: 1;
  padding: 1px;
  -webkit-transition: -webkit-transform 0.4;
  transition: -webkit-transform 0.4;
  transition: transform 0.4;
  transition: transform 0.4, -webkit-transform 0.4;
  /* Animation */
  margin: 0 auto;
}

#mainLogo:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  cursor: pointer;
  /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

#bioImage:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  cursor: pointer;
}

#bioImage {
  margin-right: 6px;
  border-radius: 3px;
  /* max-width: 100%; */
  width: 25.5vw;
  /* max-height: 66.5vh; make height auto so horizontal page shrinks will alter height factor*/
  height: auto;   
}

