/* ________________________________________________________ */

.clear {
  clear: both;
}

#main {
  background: #fff;
  width: 900px;
  padding: 35px;
  margin: 0 auto 15px auto;
  overflow: hidden;
  z-index: 1;
  box-shadow: 0 0 6px #ccc;
}

#title {
  line-height: 42px;
  border-bottom: 3px solid #000;
  padding-bottom: 30px;
  margin: 0px 0 30px 0;
  font-weight: bold;
}

#credit {
  margin-bottom: 20px;
  font-size: 11px;
  color: #999;
  text-align: center;
}
#credit a {
  color: #999;
}

p label {
  font-weight: bold;

  font-size: 110%;
  margin-right: 10px;
}
p label span.colon {
  display: inline;
}

.section {
  margin-bottom: 20px;
}

.item {
  margin: 0 0 20px 0;
}

/*  _____________________________ */
.rtl {
  direction: rtl;
}

.photo .basic-info .set {
  float: none !important;
  width: auto !important;
}
.basic-info .address {
  width: 40%;
  float: left;
}
.basic-info .r {
  float: right;
  width: 58%;
  text-align: right;
}
.basic-info label {
  font-weight: bold;
}
.basic-info label,
.basic-info span {
  display: inline !important;
}
.basic-info p {
  margin: 0;
}

.section .title {
  background: #000;
  float: left;
  text-transform: uppercase;
  font-size: 105%;
  font-weight: bold;
  word-wrap: break-word;
  color: #fff;
  padding: 4px 6px;
  width: 16%;
  margin-right: 30px;
}
.section .set {
  float: left;
  width: 78%;
}
.section .set h3 {
  margin: 0;
}
.section span.value {
  display: block;
}
.section h3 span.l,
.section span.r {
  width: 49%;
  float: left;
}
.section span.r {
  float: right;
  text-align: right;
}
.section-education .school,
.section-work .job_title {
  font-size: 100%;
  display: block;
  margin: 5px 0 5px 0;
}

/* ___ photo ___ */
#photo {
  width: 160px;
  height: 200px;
  overflow: hidden;
  float: left;
  margin-top: 8px;
  border-radius: 4px;
}
#photo img {
  width: 100%;
  height: auto;
}
.header.photo {
  margin-left: 190px;
}
.header.photo #title {
  text-align: left;
}
.header.photo .basic-info {
  float: none;
  padding: 0;
}

@media screen and (max-width: 950px) {
  body {
    background: #fff;
  }
  #main {
    box-shadow: none;
    padding: 0;
    padding: 5px;
  }
}
